import { ImageComponent } from '@ApiClient/components/image/image.component';
import { Component, ViewChild } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { User } from '@shared-types/user.interface';
import { Store } from '@ngrx/store';
import { AppStateInterface } from '@store/app/app-state.interface';
import { selectCurrentUser } from '@store/auth/auth.selectors';
import { CommonModule } from '@angular/common';
import { ImageCropperComponent } from '../image-cropper/image-cropper.component';
import { TranslateService } from '@ngx-translate/core';
import { MessageService } from 'primeng/api';

@Component({
  selector: 'app-photo-edit-profile',
  standalone: true,
  imports: [ImageComponent, CommonModule, ImageCropperComponent],
  templateUrl: './photo-edit-profile.component.html',
  styleUrl: './photo-edit-profile.component.scss',
})
export class PhotoEditProfileComponent {
  @ViewChild('photoProfile') profilePhoto!: ImageComponent;
  @ViewChild('imageCropper') imageCropper!: ImageCropperComponent;

  currentUser$: Observable<User | null> = this.store.select(selectCurrentUser);
  imageId: string | null = '';
  updatedImageUrl: string | null = null;
  isAPictureSubject: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
  isAPicture$: Observable<boolean> = this.isAPictureSubject.asObservable();
  constructor(
    private store: Store<AppStateInterface>,
    private toast: MessageService,
    private translate: TranslateService,
  ) {
    this.currentUser$.subscribe((user) => {
      this.imageId = user ? user.pictureId : null;
    });
  }

  /**
   * Delete the current profile photo. Restore the image ID and reset the profile photo object state.
   */
  onRemovePhoto() {
    this.profilePhoto.idPicture = '';
    this.profilePhoto.reset();
    this.toast.add({
      severity: 'warn',
      summary: this.translate.instant('IMAGE_CROPPER.WARNING_TITLE'),
      detail: this.translate.instant('IMAGE_CROPPER.PHOTO_INFO'),
      sticky: true,
    });
  }

  /**
   * Opens the image cropping component to allow editing of the profile photo.
   */
  openImageCropper() {
    console.log('Image cropper open');
    this.imageCropper.showDialog();
  }

  /**
   * Handles the event when a file (edited image) is selected.
   * @param file - The File object that represents the selected file.
   */
  onSelectedFile(file: File): void {
    console.log('Selected file (edited image):', file);
    this.updatedImageUrl = this.imageCropper.selectedImageBase64;
    this.isAPictureSubject.next(true);
    console.log('Profile url: ', this.updatedImageUrl);
  }
}
